import React from 'react';
import { PanelHeader, ModalRoot, ModalPage, View, Panel, ModalPageHeader, Button, PanelHeaderButton, FormLayout, Radio } from '@vkontakte/vkui';
import back from '../../resources/icons/navbar_close.svg'
import { getCategories } from '../../utils/Utils';
const MODAL_PAGE_FILTERS = 'filters';

class PeopleInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeModal: null,
      modalHistory: [],
      pickedIndex : 0,

    };

    this.onReady = this.onReady.bind(this);
  }
  modalBack = () => {
    this.setActiveModal(null);
    this.props.toogleCategoryPicker()
  };
  componentDidMount(){
    this.setActiveModal(MODAL_PAGE_FILTERS)
  }
  setActiveModal(activeModal) {
    this.setState({activeModal : activeModal})
  };
  
  onReady(){
    this.modalBack()
    this.props.toogleCategoryPicker()
  }
  render() {
    const modal = (
        <ModalRoot activeModal={this.state.activeModal} onClose={this.modalBack}>
            <ModalPage  header={
            <ModalPageHeader  left={<img onClick={this.modalBack} className="back_icon" src={back}/>}>
              <p style={{textAlign : 'center', width : '100%'}}>{this.props.people["фио"]}</p>
            </ModalPageHeader>
          } id={MODAL_PAGE_FILTERS} onClose={this.modalBack}>
                <div className="navbar_line"/>
                <p className="description_text">район : {this.props.people["район"]}</p>
                <p className="description_text">адрес : {this.props.people["м/ж"]}</p>
                <p className="description_text">Дата рождения : {this.props.people["возраст"]}</p>
                <p className="description_text">Выезд : {this.props.people["Выезд"]}</p>
                <p className="description_text">место работы : {this.props.people["место работы"]}</p>
                <p className="description_text">в ФС ушло : {this.props.people["в ФС ушло"]}</p>
                <p className="description_text">ЭПИДНОМЕР : {this.props.people["ЭПИДНОМЕР"]}</p>
                <p className="description_text">Диагноз : {this.props.people["Диагноз"]}</p>
                <p className="description_text">дата результата : {this.props.people["дата результата"]}</p>
                <p className="description_text">Учреждение зарегистрировавшее диагноз : {this.props.people["Учреждение зарегистрировавшее диагноз"]}</p>
                <p className="description_text">Пол : {this.props.people["Пол"]}</p>
                <p className="description_text">Гражданство : {this.props.people["Гражданство"]}</p>

                <div onClick={this.onReady} className="large_vk_btn hover">Закрыть</div>
                <div className="space_btn"/>
            </ModalPage>
        </ModalRoot>
    );
    return (
        <View activePanel="modals" modal={modal}>
          <Panel id="modals">
            
          </Panel>
        </View>
      );
  }
}

export default PeopleInfo;