import React from 'react';
import '../resources/styles/map.css'
import { PanelHeader, HorizontalScroll } from '@vkontakte/vkui';
import NavbarBack from '../panels/NavbarBack';
import GoogleMapReact from 'google-map-react';
import Supercluster from 'supercluster';
import MarkerCircle from './custom_views/MarkerCircle';
import close_dark_24 from '../resources/icons/close_dark_24.svg'
import empty from '../resources/icons/empty.svg'
import search from '../resources/icons/search.svg'
import MapFilterDropDown from './custom_views/MapFilterDropdown';
import BottomSheetDialog from './custom_views/BottomSheetDialog';
import CategoryItem from './custom_views/CategoryItem';
import add_white from '../resources/icons/add_white.svg'
import xlsxParser from 'xlsx-parse-json';
import { getCategories } from '../utils/Utils';
import Geocode from "react-geocode";
import AreasPicker from './custom_views/AreasPicked';
import { all } from 'q';
import PeopleInfo from './custom_views/PeopleInfo';
class Map extends React.Component {
    static defaultProps = {
        center: {
          lat: 59.95,
          lng: 30.33
        },
        zoom: 12
      };
    constructor(props){
        super(props)
        this.state = {
            isShowToolbar : true,
            isShowBottomSheet : false,
            filterMood : 0,
            categories : getCategories(),
            filteredCategories : getCategories(),
            filteredNews : this.props.news,
            filteredPeople : [],
            allPeople : [],
            areas : ["Все районы"],
            searchText : "",
            areaPicked : "Все районы",
            filterCategory : undefined,
            pickedJson : undefined,
            isShowAreasPicker : false,
            isShowPeople : false,
            peoplePicked : undefined
        }
        this.closeMap = this.closeMap.bind(this);
        this.toogleBottomSheet = this.toogleBottomSheet.bind(this);
        this.filterMoodPicked = this.filterMoodPicked.bind(this);
        this.onSearchChanged = this.onSearchChanged.bind(this);
        this.filter = this.filter.bind(this);
        this.filterCategoryPicked = this.filterCategoryPicked.bind(this);
        this.onMarkerClicked = this.onMarkerClicked.bind(this);
        this.fileChangedHandler = this.fileChangedHandler.bind(this);
        this.handleTimeout = this.handleTimeout.bind(this);
        this.openAreasPicker = this.openAreasPicker.bind(this);
        this.closeAreasPicker = this.closeAreasPicker.bind(this);
        this.onAreaPicked = this.onAreaPicked.bind(this);
        this.openPeople = this.openPeople.bind(this);
        this.closePeople = this.closePeople.bind(this);

        this.fileUpload = React.createRef();
        this.currentIndex = 0
        
    }
    pickFile(){
        this.fileUpload.current.click();
    }
    componentDidMount(){
       // setTimeout(this.disableToolbar, 1000)
        this.setState({isShowToolbar : false})
        this.filter(this.state.searchText, this.state.filterMood, this.state.filterCategory)
        this.props.setMarkerItem({})
    }
    disableToolbar = () => {
        // if (window.document.getElementsByClassName("PanelHeader")[0] != undefined){
        //     window.document.getElementsByClassName("PanelHeader")[0].style.display = 'none'
        // }   
    }
    getLatLongs = (pickedJson) => {
        Geocode.setApiKey("AIzaSyDJrwFDlj0KxrgP6W91C08IhDNJ1Zr8ANA");
        Geocode.setLanguage("ru");
        Geocode.enableDebug();
        this.getLatLongRequestLoop(pickedJson)
       
    }
    openPeople(){
        this.setState({isShowPeople : true})
    }
    closePeople(){
        this.setState({isShowPeople : false})
    }
    openAreasPicker(){
        this.setState({isShowAreasPicker : true})
    }
    closeAreasPicker(){
        this.setState({isShowAreasPicker : false})
    }
    onAreaPicked(area){
        this.setState({areaPicked : area})
        this.filterArea(area, this.state.allPeople)
    }
    getLatLongRequest = (data) => {
        Geocode.fromAddress(data["м/ж"]).then(
            response => {
                this.currentIndex ++
                const { lat, lng } = response.results[0].geometry.location;
                console.log(lat, lng);
                if (this.currentIndex < this.state.pickedJson.length){
                    var allPeople = this.state.allPeople
                    data.lat = lat
                    data.lng = lng
                    allPeople.push(data)
                    this.setState({allPeople : allPeople})
                    this.getLatLongRequest(this.state.pickedJson[this.currentIndex])
                
                } else {
                    this.currentIndex = 0
                    console.log()
                }
                
            },
            error => {
                console.error(error);
            }
          );
    }
    getLatLongRequestLoop = (dataArray) => {
        dataArray.forEach(data => {
            if (this.addToLocalStorage(data)){
                this.currentIndex += 1
                setTimeout(this.handleTimeout(data), (this.currentIndex * 30))
            }
            
        });
    }
    addToLocalStorage = (data) => {
        var localAdresses = JSON.parse(localStorage.getItem('adresses'))
        if (localAdresses != null && localAdresses != undefined && localAdresses.length > 0){
            localAdresses.forEach(element => {
                if (element["м/ж"] == data["м/ж"]){
                    var allPeople = this.state.allPeople
                    data.lat = element.lat
                    data.lng = element.lng
                    allPeople.push(data)
                    this.setState({allPeople : allPeople, filteredPeople : allPeople})

                    var areas = this.state.areas
                    if (!areas.includes(data["район"])){
                            areas.push(data["район"])
                            this.setState({areas : areas})
                    }
                }
            });
            return false
        } else {
            return true
        }
        
    }
    handleTimeout(data){
           var areas = this.state.areas
           if (!areas.includes(data["район"])){
                areas.push(data["район"])
                this.setState({areas : areas})
            }
        Geocode.fromAddress(data["м/ж"]).then(
            response => {
                const { lat, lng } = response.results[0].geometry.location;
                console.log(lat, lng);
                    var allPeople = this.state.allPeople
                    data.lat = lat
                    data.lng = lng
                    allPeople.push(data)
                    var localAdresses = JSON.parse(localStorage.getItem('adresses'))
                    if (localAdresses != null && localAdresses != undefined && localAdresses.length > 0){
                        localAdresses.push(data)
                    } else {
                        localAdresses = [data]
                    }
                    const serialized = JSON.stringify(localAdresses);
                    localStorage.setItem('adresses', serialized);
                    this.setState({allPeople : allPeople, filteredPeople : allPeople})
                    
            },
            error => {
                console.error(error);
            }
          );       
    }
    filterMoodPicked(mood){
        this.setState({filterMood : mood})
        this.filter(this.state.searchText, mood, this.state.filterCategory)
    }
    filterCategoryPicked(category){
        this.setState({filterCategory : category})
        this.filter(this.state.searchText, this.state.filterMood, category)
    }
    toogleBottomSheet(){
        this.setState({isShowBottomSheet : true})
    }
    closeBottomSheet = () =>{
        this.setState({isShowBottomSheet : false})
    }
    closeMap(){
        this.setState({isShowToolbar : true})
        this.props.changePage("news")
    }
    onMarkerClicked(people){
       this.openPeople()
       this.setState({peoplePicked : people})
    }

    onSearchChanged(event){
        if (event){
            var value = event.target.value
            this.setState({searchText : value})
            //this.filter(value, this.state.filterMood, this.state.filterCategory)
            this.filterCategories(value)
        }
        
    }
    filterArea(area, allPeople){
        const result = allPeople.filter(item => {
            return (area == "Все районы") ? true : (item["район"] == area ? true : false)
        });
        this.setState({filteredPeople : result})
    }
    filter(text, filterMood, filterCategory){
        const result = this.props.news.filter(post => {
            const isMood = post.category.mood == filterMood
            const isCategory = (filterCategory == undefined ? true : post.category.title.toLowerCase() == filterCategory.title.toLowerCase())
            return isMood && isCategory
        });
        this.setState({filteredNews : result})
    }
    filterCategories(text){
        const result = this.state.categories.filter(category => {
            return (category.title.toLowerCase().includes(text.toLowerCase()))
        });
        this.setState({filteredCategories : result})
    }
    addFile = () => {
        this.pickFile()
    }
    fileChangedHandler(event) {
        if(event && event.target.files && event.target.files.length > 0){
            // const urlObj = URL.createObjectURL(event.target.files[0])
            const file = event.target.files[0]
            xlsxParser
            .onFileSelection(file)
            .then(data => {
                var parsedData = data.Лист1;
                this.setState({pickedJson : parsedData})
                this.getLatLongs(parsedData)

                console.log("parsedData = " + JSON.stringify(parsedData))
            });
            
        }
    }
    render() {

        return (
            <div className="news_content">
                {this.state.isShowToolbar && <PanelHeader>
                    <NavbarBack back="news" onBack={this.changePage} title="Новости"/>
                </PanelHeader>}
                <div style={{ height: '100vh', width: '100%' }} className="map_container">
                    <GoogleMapReact bootstrapURLKeys={{ key: "AIzaSyDJrwFDlj0KxrgP6W91C08IhDNJ1Zr8ANA" }} defaultCenter={this.props.center}
                           defaultZoom={this.props.zoom}>
                               {this.state.filteredPeople.length > 0 && this.state.filteredPeople.map((item, index) => (
                                    <MarkerCircle onMarkerClicked={this.onMarkerClicked} lng={item.lng} lat={item.lat} index={index} post={item} key={index}/>
                                ))}
                           

                    </GoogleMapReact>
                
                </div>
                <MapFilterDropDown filterMood={this.state.areaPicked} toogleBottomSheet={this.openAreasPicker}/>
                {this.state.isShowBottomSheet && <BottomSheetDialog filterMoodPicked={this.filterMoodPicked} onClose={this.closeBottomSheet}/>}
                {/* <div onClick={this.closeMap} className="close_map_container">
                    <img className="close_map_icon" src={close_dark_24}/>
                </div> */}
                {/* <div className="map_search_bottom_container">
                    <div className="search_music_container">
                        <img className="search_icon_input" src={search}/>
                        <input className="search_input" placeholder="Поиск" onChange={this.onSearchChanged} value={this.state.searchText} />
                    </div>
                    {this.state.filteredCategories.length > 0 ? <HorizontalScroll>
                        <div style={{ display: 'flex', marginLeft : '4px'}}>
                            {this.state.filteredCategories.length > 0 && this.state.filteredCategories.map((item, index) => (
                                <CategoryItem filterCategory={this.state.filterCategory} filterCategoryPicked={this.filterCategoryPicked} index={index} category={item} key={index}/>
                            ))}
                        </div>
                    </HorizontalScroll> :
                    <p className="nothing_found_cat">Ничего не найдено</p>
                </div>  */}
                {this.state.filteredNews.length == 0 && <div className="empty_filters">
                    <div className="center_empty_container">
                        <img className="horizontal-center relative" src={empty}/>
                        <p className="empty_title">Нет записей с таким<br/>настроением</p>
                        <p className="empty_title font_16">Выберите другое</p>
                    </div>
                </div>}
                <div onClick={this.addFile} className="btn_planet">
                    <img className="plus_add" src={add_white}/>
                </div>
                
                <input id="fileInput" className="custom-file-input hover" type="file" onChange={this.fileChangedHandler} style={{ display: "none" }} ref={this.fileUpload}/>
                {this.state.isShowAreasPicker && <AreasPicker areas={this.state.areas} areaPicked={this.state.areaPicked} onCategoryPicked={this.onAreaPicked} toogleCategoryPicker={this.closeAreasPicker}/>}
                {this.state.isShowPeople && <PeopleInfo people={this.state.peoplePicked} toogleCategoryPicker={this.closePeople}/>}
            </div>
        );
    }
}
export default Map;